.toolPanelBase .dockingPanelTitle span,
.toolPanelBase .dockingPanelTitle span:before {
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-size: 100%;
  margin-right: 10px;
  height: 24px;
  float: left;
  width: 24px; }

.toolPanelBase .dockingPanelClose {
  position: absolute;
  top: 0px; }

.toolPanelBase .dockingPanelTitle {
  padding: 4px 10px 4px 10px;
  text-transform: none;
  text-align: left; }

.dockingPanel .dockingPanelTitle .dockingPanelTitleText {
  width: calc(100% - 55px);
  text-transform: none;
  position: relative;
  overflow: hidden;
  margin-top: 4px;
  height: 20px; }
